<template>
  <div class="wrapper" id="app">
    <div class="contents-wrapper">
      <page-header />
      <router-view/>
      <page-footer />
    </div>
  </div>
</template>

<script>
import PageHeader from './components/PageHeader'
import PageFooter from './components/PageFooter'
export default {
  name: 'App',
  components: {
    PageHeader,
    PageFooter
  }
}
</script>

<style lang="scss">
@import './assets/css/reset.css';
.wrapper {
  background: #faf2e5;
  position: relative;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  .contents-wrapper {
    min-width: 990px;
    max-width: 100%;
    min-height: 350px;
    background: #fff;
  }
}
</style>



